import { applyCurrentFilter, updateClearCompletedButton, updateItemsLeft } from "./info";
import { clearPreviewTimer, updateMirrorPosition } from '../utils/dragUtils';
import {
	draggingEl,
	isDragging,
	targetEl,
	mirrorEl,
	previewTimer,
	isMouseDown,
	dragStartX,
	dragStartY,
	clickSuppressedByDrag,
	originalNextSibling,
	previewMoved,
	resetDragState
} from '../state/dragState';

let todoIdCounter = 0;
const DRAG_THRESHOLD = 5;

export function renderList(): HTMLElement {
	const list = document.createElement("ul");
	list.id = "todo-list";
	return list;
}

export function setupInputHandler() {
	const input = document.getElementById("todo-input") as HTMLInputElement;
	const list = document.getElementById("todo-list") as HTMLUListElement;

	if (!input || !list) return;

	input.addEventListener("keydown", (event) => {
		if (event.key === "Enter") {
			const value = input.value.trim();
			if (!value) return;
			createTodoItem(value);
			input.value = "";
		}
	});
}

function createTodoItem(value: string) {
	const list = document.getElementById("todo-list") as HTMLUListElement;
	const li = document.createElement("li");
	li.className = "todo-item";
	li.dataset.id = (todoIdCounter++).toString();

	const span = document.createElement("span");
	span.textContent = value;
	span.className = "todo-text";

	const deleteButton = document.createElement("button");
	deleteButton.textContent = "삭제";
	deleteButton.className = "delete-button";

	li.append(span, deleteButton);
	list.prepend(li);

	li.addEventListener("click", (e) => {
		if ((e.target as HTMLElement).tagName === "BUTTON" || clickSuppressedByDrag) {
			clickSuppressedByDrag.value = false;
			return;
		}
		span.classList.toggle("completed");
		sortList();
		applyCurrentFilter();
		updateItemsLeft();
		updateClearCompletedButton();
	});

	li.addEventListener("mousedown", (e) => {
		if (span.classList.contains("completed")) return;
		isMouseDown.value = true;
		draggingEl.value = li;
		dragStartX.value = e.clientX;
		dragStartY.value = e.clientY;
		document.addEventListener("mousemove", detectDragStart);
		document.addEventListener("mouseup", cancelDetectDragStart);
	});

	deleteButton.addEventListener("click", () => {
		li.remove();
		updateItemsLeft();
		updateClearCompletedButton();
	});

	updateItemsLeft();
	updateClearCompletedButton();
}

export function sortList(): void {
	const list = document.getElementById("todo-list") as HTMLUListElement;
	const items = Array.from(list.children) as HTMLElement[];

	const activeItems = items
		.filter((item) => !item.querySelector(".todo-text")?.classList.contains("completed"))
		.sort((a, b) => Number(b.dataset.id) - Number(a.dataset.id));

	const completedItems = items.filter((item) => item.querySelector(".todo-text")?.classList.contains("completed"));

	list.innerHTML = "";
	[...activeItems, ...completedItems].forEach((item) => list.appendChild(item));
}

function detectDragStart(e: MouseEvent) {
	if (!isMouseDown.value || !draggingEl.value) return;
	const dx = Math.abs(e.clientX - dragStartX.value);
	const dy = Math.abs(e.clientY - dragStartY.value);
	if (dx > DRAG_THRESHOLD || dy > DRAG_THRESHOLD) {
		startDrag(e);
		cancelDetectDragStart();
	}
}

function cancelDetectDragStart() {
	isMouseDown.value = false;
	document.removeEventListener("mousemove", detectDragStart);
	document.removeEventListener("mouseup", cancelDetectDragStart);
}

function startDrag(e: MouseEvent) {
	originalNextSibling.value = draggingEl.value!.nextSibling;
	isDragging.value = true;
	draggingEl.value!.style.opacity = "0.3";

	mirrorEl.value = draggingEl.value!.cloneNode(true) as HTMLElement;
	mirrorEl.value.classList.add("mirror");
	const rect = draggingEl.value!.getBoundingClientRect();
	mirrorEl.value.style.width = `${rect.width}px`;
	mirrorEl.value.style.height = `${rect.height}px`;
	document.body.appendChild(mirrorEl.value);

	updateMirrorPosition(e.clientX, e.clientY, mirrorEl.value);

	document.addEventListener("mousemove", handleMouseMove);
	document.addEventListener("mouseup", handleMouseUp);
	document.addEventListener("keydown", handleKeyDown);
}

function handleMouseMove(e: MouseEvent) {
	if (!isDragging.value || !draggingEl.value) return;
	updateMirrorPosition(e.clientX, e.clientY, mirrorEl.value);
	const list = document.getElementById("todo-list")!;
	const rect = list.getBoundingClientRect();

	if (e.clientX < rect.left || e.clientX > rect.right || e.clientY < rect.top || e.clientY > rect.bottom) {
		targetEl.value?.style.removeProperty("border-left");
		targetEl.value = null;
		clearPreviewTimer(previewTimer.value);
		return;
	}

	const afterElement = getDragAfterElement(list, e.clientY);
	if (targetEl.value && targetEl.value !== afterElement) {
		targetEl.value.style.removeProperty("border-left");
		clearPreviewTimer(previewTimer.value);
	}
	targetEl.value = afterElement;

	if (targetEl.value && !targetEl.value.querySelector(".todo-text")?.classList.contains("completed")) {
		targetEl.value.style.borderLeft = "4px solid limegreen";
		clearPreviewTimer(previewTimer.value);
		previewTimer.value = window.setTimeout(() => movePreview(), 2000);
	}
}

function movePreview() {
	if (!targetEl.value || !draggingEl.value) return;
	const list = document.getElementById("todo-list")!;
	const targetRect = targetEl.value.getBoundingClientRect();
	const draggingRect = draggingEl.value.getBoundingClientRect();
	const isMovingDown = draggingRect.top < targetRect.top;
	const nextEl = targetEl.value.nextSibling;
	targetEl.value.style.removeProperty("border-left");

	if (isMovingDown && nextEl !== draggingEl.value) {
		list.insertBefore(draggingEl.value, nextEl);
	} else if (!isMovingDown && targetEl.value !== draggingEl.value) {
		list.insertBefore(draggingEl.value, targetEl.value);
	}
	targetEl.value = draggingEl.value;
	targetEl.value.style.borderLeft = "4px solid limegreen";
	previewMoved.value = true;
}

function handleMouseUp(e: MouseEvent) {
	if (!draggingEl.value) return;
	const list = document.getElementById("todo-list")!;
	const rect = list.getBoundingClientRect();

	if (e.clientX < rect.left || e.clientX > rect.right || e.clientY < rect.top || e.clientY > rect.bottom) {
		if (originalNextSibling.value) list.insertBefore(draggingEl.value, originalNextSibling.value);
		cleanupDrag();
		return;
	}

	if (mirrorEl.value?.parentNode) mirrorEl.value.remove();
	targetEl.value?.style.removeProperty("border-left");
	draggingEl.value.style.opacity = "1";
	clickSuppressedByDrag.value = true;
	cleanupDrag();
}

function handleKeyDown(e: KeyboardEvent) {
	if (e.key === "Escape") {
		const list = document.getElementById("todo-list")!;
		if (draggingEl.value && originalNextSibling.value) list.insertBefore(draggingEl.value, originalNextSibling.value);
		cleanupDrag();
		clickSuppressedByDrag.value = true;
	}
}

function getDragAfterElement(container: HTMLElement, y: number): HTMLElement | null {
	const elements = document.elementsFromPoint(window.innerWidth / 2, y);
	return elements.find((el) => el.classList.contains("todo-item")) as HTMLElement | null;
}

function cleanupDrag() {
	resetDragState();
	document.removeEventListener("mousemove", handleMouseMove);
	document.removeEventListener("mouseup", handleMouseUp);
	document.removeEventListener("keydown", handleKeyDown);
}
