export const draggingEl = { value: null as HTMLElement | null };
export const isDragging = { value: false };
export const targetEl = { value: null as HTMLElement | null };
export const mirrorEl = { value: null as HTMLElement | null };
export const previewTimer = { value: null as number | null };
export const isMouseDown = { value: false };
export const dragStartX = { value: 0 };
export const dragStartY = { value: 0 };
export const clickSuppressedByDrag = { value: false };
export const originalNextSibling = { value: null as ChildNode | null };
export const previewMoved = { value: false };

export function resetDragState() {
	draggingEl.value = null;
	isDragging.value = false;
	targetEl.value = null;
	mirrorEl.value = null;
	previewTimer.value = null;
	isMouseDown.value = false;
	dragStartX.value = 0;
	dragStartY.value = 0;
	clickSuppressedByDrag.value = false;
	originalNextSibling.value = null;
	previewMoved.value = false;
}
